import { takeEvery, all } from "redux-saga/effects";
import * as saga from "./member";

export function* watchMember(){
  yield all([
    takeEvery("MEMBER_REGISTRASI", saga.register),
    takeEvery("MEMBER_LOGIN", saga.login),
    takeEvery("MEMBER_CHECK_STATE", saga.authCheckState),
    takeEvery("MEMBER_GET_DATA",saga.getUserData),
    takeEvery("MEMBER_LOGOUT", saga.logout),
    takeEvery("MEMBER_UPDATE_PROFILE", saga.updateProfile),
  ])
}
