import React, {Component} from 'react';
import './App.css';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import {BrowserRouter, Route, Link, Switch} from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Akun from './Akun.jsx';





class Follow extends React.Component {
  render (){
      return(
        <div className="animated fadeIn" >
          <Header />
          <DisabledTabs />
          <main>
          <Switch>
            <Route path='/summary' exact component={Summary} />
           <Route path='/instituation' exact component={Instituation} />
           <Route path='/similar' exact component={Similar} />
           <Route path='/' component={Summary} />
          </Switch>
          </main>
  			</div>
      );

  }
}
export default Follow;

function Header(){
  return(
    <div className="App judul">
      <h1> Follow with related researchers </h1>
      <p> Find and follow researchers to grow your network and keep up with their work.</p>
    </div>
  );
}

function DisabledTabs() {
  const [value, setValue] = React.useState(1);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Paper square>
      <Tabs

        indicatorColor="primary"
        textColor="primary"
        onChange={handleChange}
        aria-label="disabled tabs example"
      >
      <Link to='/summary'>  <Tab label="Summary" /> </Link>
      <Link to='/instituation'>  <Tab label="Your instituation" /> </Link>
      <Link to='/similar'>  <Tab label="Similar Interest" /> </Link>
      </Tabs>
    </Paper>
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

function Summary(){
  const classes = useStyles();
  return(
    <div className={classes.root}>
    <br/>
    <Grid container spacing={3}>
      <Grid item xs={3}>
        <Paper className={classes.paper}><AccountCircleIcon style={{ fontSize: 80 }} />
        <Button variant="outlined" color="primary">
          Follow
        </Button>
        <br/> <Link to= '/akun'><a> Alvinanda </a> </Link> <br/>
        <a> Brawijaya University </a><br/>
        <a> Informatika </a></Paper>
      </Grid>
      <Grid item xs={3}>
        <Paper className={classes.paper}><AccountCircleIcon style={{ fontSize: 80 }} />
        <Button variant="outlined" color="primary">
          Follow
        </Button>
        <br/><a> Peter </a><br/>
        <a> Malang University </a><br/>
        <a> Pertanian </a></Paper>
      </Grid>
    </Grid>
    </div>
  );
}

