class TambahPosting extends React.Component{

	render(){

		return(

			<div className="animated fadeIn">
        <Header />
        <Data />
			</div>

		)
	}
}

export default TambahPosting;

function Header(){
  return(
    <div className="App judul">
      <h1> Tambah Postingan </h1>
      <p> Bagikan Peneletian atau Pengabdian anda</p>
    </div>
  );
}

function Data() {
	return(
		<Row>
		<Col>
		<Card>
			<CardBody>
				<Form  className="form-horizontal">
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">Judul</Label>
						<Col sm="6">
							<Input type="text" id="input-normal" name="input-small" placeholder="" />
						</Col>
					</FormGroup>
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">Tanggal Terbit</Label>
						<Col sm="6">
							<Input type="text" id="input-normal" name="input-normal" placeholder="bulan,tahun"  />
						</Col>
					</FormGroup>
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">Abstract</Label>
						<Col sm="6">
							<Input type="textarea" id="input-normal" name="input-large" placeholder=""  />
						</Col>
					</FormGroup>
          <FormGroup row>
						<Label sm="5" htmlFor="input-normal">File</Label>
						<Col sm="6">
            <Input type="file" name="file" id="exampleFile" />
            <FormText color="muted">
              File harus berformat pdf.
            </FormText>
						</Col>
					</FormGroup>
        <Link to='/Home'>  <Button variant="contained" color="primary">
            Submit
          </Button>
          </Link>
				</Form>
			</CardBody>
		</Card>
		</Col>
		</Row>

	);
}
