import React, {Fragment, useEffect} from 'react';
import './App.css';
import {BrowserRouter, Route, Link, Switch, Redirect} from 'react-router-dom';
import Akun from './Akun.jsx';
import Navbar from './Header';
import Home from './Home';
import Jurnal from './Jurnal';
import Follow from './Follow';
import Summary from './Follow';
import Instituation from './Follow';
import Similar from './Follow';
import Jumboo from './Jumbo';
import Post from './Post';
import TambahPosting from './TambahPosting';
import EditPosting from './EditPosting';
import Biodata from './Jumbo';
import Penelitian from './Jumbo';
import Pengabdian from './Jumbo';
import PostAkun from './Jumbo';
import EditProfile from './EditProfile';
import Login from './Login';
import Chat from './Chat';
import Chatting from './components/App';
import Register from './Register';
import Overview from './komponen/Overview';
import TabelBuku from './komponen/TabelBuku';
import Spinner from './spinner/Spinner';
import { useLocation, useHistory } from 'react-router';
import { useSelector, useDispatch } from 'react-redux';
import * as actions from './store/actions/member';


function MyRouter(){

  const history = useHistory();
  const idToken = localStorage.getItem("idToken");
  const idTokenRedux = useSelector(state => state.member.idToken)


  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(actions.authCheckState());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dispatch]);

  const autentikasi = () => {
    if(idToken){
      return (
        <Fragment>
        <Route path="/chat" exact component={Chatting}></Route>
        <Route path="/tambahPosting" exact component={TambahPosting}></Route>
        <Route path="/editProfile" exact component={EditProfile}></Route>
        <Route path="/editPosting" exact component={EditPosting}></Route>
        <Route path="/post" exact component={Post}></Route>
        <Route path="/akun" exact component={Akun}></Route>
        <Route path="/profile" exact component={Jumboo}></Route>
        <Route path="/home" exact component={Home}></Route>
        <Route path="/follow" exact component={Follow}></Route>
        <Route path="/jurnal" exact component={Jurnal}></Route>
        <Route path='/summary' exact component={Summary} />
         <Route path='/instituation' exact component={Instituation} />
         <Route path='/similar' exact component={Similar} />
         <Route path='/postAkun' exact component={PostAkun} />
          <Route path='/biodata' exact component={Biodata} />
          <Route path='/penelitian' exact component={Penelitian} />
          <Route path='/pengabdian' exact component={Pengabdian} />
          <Route path='/overview' exact component={Overview} />
        </Fragment>
      );
    }else {
      return null;
    }
  }

  if(idToken && !idTokenRedux) {
    return <Spinner />
  }else return(
    <Fragment>
        <div>
        {idToken ? (<Navbar />) : null}
        </div>
      <Switch>
        <Route path ='/' exact component ={Login} />
        <Route path="/register" exact component={Register} />
        {autentikasi()}
        <Redirect to="/" />
      </Switch>
    </Fragment>
  );

}

export default MyRouter;
