import React, {Component} from 'react';
import './App.css';
import Pengabdian from './komponen/Pengabdian';
import Penelitian from './komponen/Penelitian';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import {BrowserRouter, Route, Link, Switch} from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import ThumbUpAltOutlinedIcon from '@material-ui/icons/ThumbUpAltOutlined';
import Post from './Post';
import {
  Badge,
  ButtonGroup,
  ButtonToolbar,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
	CardTitle,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';

class Jurnal extends React.Component{
  render(){
    return(
      <BrowserRouter>
        <div className="animated fadeIn" >
          <Header />
          <DisabledTabs />
          <main>
            <Switch>
              <Route path='/penelitiann' exact component={Penelitiann} />
              <Route path='/pengabdiann' exact component={Pengabdiann} />
              <Route path='/post' exact component={Post} />
              <Route path='/' component={Penelitiann} />
            </Switch>
          </main>
          </div>
          </BrowserRouter>
    );
  }
}

export default Jurnal;

function Header(){
  return(
    <div className="App judul">
      <h1> Research </h1>
      <p> Find research to grow your world.</p>
    </div>
  );
}

function DisabledTabs() {
  const [value, setValue] = React.useState(1);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Paper square>
      <Tabs

        indicatorColor="primary"
        textColor="primary"
        onChange={handleChange}
        aria-label="disabled tabs example"
      >
      <Link to='/penelitiann'>  <Tab label="Penelitian" /> </Link>
      <Link to='/pengabdiann'>  <Tab label="Pengabdian" /> </Link>
      </Tabs>
    </Paper>
  );
}

function Pengabdiann(){
  return(
    <div>
    <Pengabdian name="nanda" judul="Rancang Bangun Sistem Informasi Manajemen Kelurahan (Studi Kasus di Kelurahan Kauman dan Kasin Kota Malang)"/>
    <Link to='/post'>
      <Button>
        <a className="App-comment"> <ChatBubbleOutlineIcon /> Comment </a>
      </Button>
    </Link>
    <Button> <a className="App-comment"> <ThumbUpAltOutlinedIcon /> Like </a></Button>
    <br/>
    <br/>
    </div>
  );
}

function Penelitiann(){
  return(
    <div>
      <Penelitian name="Alvin" judul="Pengembangan Sistem Repository dan Sosial Media untuk Dosen Berbasis Website"/>
      <Link to='/post'>
        <Button>
          <a className="App-comment"> <ChatBubbleOutlineIcon /> Comment </a>
        </Button>
      </Link>
      <Button> <a className="App-comment"> <ThumbUpAltOutlinedIcon /> Like </a></Button>
      <br/>
      <br/>
    </div>
  );
}
