export default function Jumboo() {

const member = useSelector(state => state.member);

		return(
			<div className="animated fadeIn">
				<img src={logo} className="App-profil" alt="logo" />
				<a className="App-nama">{member.name}</a>
        <Link to ='/editProfile'><Button variant="contained" size="small">Setting</Button> </Link>
				<DisabledTabs  />
			</div>

		)
	}

  function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`full-width-tabpanel-${index}`}
        aria-labelledby={`full-width-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box p={3}>
            <Typography>{children}</Typography>
          </Box>
        )}
      </div>
    );
  }

  TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
  };

  function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
root: {
  flexGrow: 1,
  backgroundColor: theme.palette.background.paper,
},
}));

function DisabledTabs() {
  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleChangeIndex = (index) => {
     setValue(index);
   };
  return (
    <div className={classes.root}>
     <AppBar position="static" color="default">
     <Tabs
        value={value}
        onChange={handleChange}
        indicatorColor="primary"
        textColor="primary"
        variant="fullWidth"
        aria-label="full width tabs example"
      >
         <Tab label="Overview" {...a11yProps(0)} />
         <Tab label="Post" {...a11yProps(1)} />
         <Tab label="Biodata" {...a11yProps(2)} />
         <Tab label="Penelitian" {...a11yProps(3)} />
         <Tab label="Pengabdian" {...a11yProps(4)} />
         <Tab label="Buku" {...a11yProps(5)} />
         <Tab label="HKI" {...a11yProps(6)} />
       </Tabs>
     </AppBar>

     <SwipeableViews
       axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
       index={value}
       onChangeIndex={handleChangeIndex}
     >

     <TabPanel value={value} index={0} dir={theme.direction}>
       <Overview />
     </TabPanel>
     <TabPanel value={value} index={1} dir={theme.direction}>
       <PostAkun />
     </TabPanel>
     <TabPanel value={value} index={2} dir={theme.direction}>
       <Biodata />
     </TabPanel>
     <TabPanel value={value} index={3} dir={theme.direction}>
       <Penelitiann />
     </TabPanel>
     <TabPanel value={value} index={4} dir={theme.direction}>
       <Pengabdiann />
     </TabPanel>
     <TabPanel value={value} index={5} dir={theme.direction}>
       <TabelBuku />
     </TabPanel>
     <TabPanel value={value} index={6} dir={theme.direction}>
       <TabelHKI />
     </TabPanel>
     </SwipeableViews>
   </div>
  );
}

function PostAkun(){
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [open, setOpen] = React.useState(false);


  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClosed = () => {
    setOpen(false);
  };

  return (
    <div>
      <Penelitian name="Alvin" judul="Pengembangan Sistem Repository dan Sosial Media untuk Dosen Berbasis Website"/>
      <Link to='/post'>
      <Button> <a className="App-comment"> <ChatBubbleOutlineIcon /> Comment </a></Button>
      </Link>
      <Button> <a className="App-comment"> <ThumbUpAltOutlinedIcon /> Like </a></Button>
      <Button className="f" aria-controls="simple-menu" aria-haspopup="true" color="primary" onClick={handleClick}>
        Opsi
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
      <Link to="/editPosting">
       <MenuItem onClick={handleClose}>Edit</MenuItem>
       </Link>

      </Menu>
      <br/>
      <br/>
      <Pengabdian name="nanda" judul="Rancang Bangun Sistem Informasi Manajemen Kelurahan (Studi Kasus di Kelurahan Kauman dan Kasin Kota Malang)"/>
      <Link to='/post'>
      <Button> <a className="App-comment"> <ChatBubbleOutlineIcon /> Comment </a></Button>
      </Link>
      <Button> <a className="App-comment"> <ThumbUpAltOutlinedIcon /> Like </a></Button>
      <Button className="f" aria-controls="simple-menu" aria-haspopup="true" color="primary" onClick={handleClick}>
        Opsi
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <Link to='/editPosting'>
        <MenuItem onClick={handleClose}>Edit</MenuItem>
        </Link>
        <MenuItem onClick={handleClickOpen}>Hapus</MenuItem>
        <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Hapus Posting?"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Apakah anda yakin ingin menghapus postingan?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={handleClosed} color="primary">
            Tidak
          </Button>
          <Button onClick={handleClosed} color="primary" autoFocus>
            Ya
          </Button>
        </DialogActions>
      </Dialog>
      </Menu>
    </div>
  );
}

