import React, {Component} from 'react';
import './App.css';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import TextField from '@material-ui/core/TextField';
import ThumbUpAltOutlinedIcon from '@material-ui/icons/ThumbUpAltOutlined';
import Komen from './Komen';
import Pengabdian from './komponen/Pengabdian';
import Penelitian from './komponen/Penelitian';
import {
  Badge,
  ButtonGroup,
  ButtonToolbar,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
	CardTitle,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';


class Post extends Component {
 render() {
		return (

      <div className="animated fadeIn">
      <Penelitian name="Alvin" judul="Pengembangan Sistem Repository dan Sosial Media untuk Dosen Berbasis Website"/>
      <Abstract />
      <Button> <a className="App-comment"> <ChatBubbleOutlineIcon /> Comment </a></Button>
      <Button> <a className="App-comment"> <ThumbUpAltOutlinedIcon /> Like </a></Button>
      <br/><br/>
      <Komen />
      </div>

	  );
	}
}

export default Post;

function Abstract(props){
	return(
		<Row>
			<Col>
				<Card>
					<CardBody>
						<Row>
							<Col sm="5">
								<CardTitle className="mb-0">Abstract</CardTitle>
							</Col>
						</Row>
					</CardBody>
					<CardFooter>
					<p>Ekstraksi topik adalah alat penting untuk membantu mengumpulkan informasi dari sejumlah besar sumber. Makalah ini memperkenalkan pendekatan baru untuk mengekstrak topik dari kumpulan dokumen teks. Untuk mendapatkan topik, langkah preprocessing dilakukan untuk menghapus bagian dokumen yang tidak perlu. Kemudian, istilah frekuensi dokumen invers-frekuensi dibangun berdasarkan bobot dalam dokumen. Setelah itu, transformasi fitur berbasis SVD terlibat dalam membangun fitur yang digunakan untuk pengelompokan. Sebelum proses pengelompokan, algoritma Pilar dijalankan untuk memilih centroid awal untuk pengelompokan K-Means. Akhirnya, bobot istilah dalam cluster dihitung menggunakan term-cluster weight sebagai dasar untuk memilih topik dari cluster. Berdasarkan hasil eksperimental, disimpulkan bahwa kerangka kerja mencapai hasil yang memuaskan dengan mencapai akurasi 100%, 95,1%, 83,7%, dan 68,7% untuk 4 topik yang diperoleh dari kategori Binary2, Multi5, Multi7, dan Multi10 dari 20Newsgroup Himpunan data.</p>
					</CardFooter>
				</Card>
			</Col>
		</Row>

	);
}
