class EditPosting extends React.Component{

	render(){

		return(

			<div className="animated fadeIn">
        <Header />
        <Data />
			</div>

		)
	}
}

export default EditPosting;

function Header(){
  return(
    <div className="App judul">
      <h1> Edit Postingan </h1>
      <p> Benahi postingan anda</p>
    </div>
  );
}

function Data() {
	return(
		<Row>
		<Col>
		<Card>
			<CardBody>
				<Form  className="form-horizontal">
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">Judul</Label>
						<Col sm="6">
							<Input type="text" id="input-normal" name="input-small" placeholder="Pengembangan Sistem Repository dan Sosial Media untuk Dosen Berbasis Website " />
						</Col>
					</FormGroup>
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">Tanggal Terbit</Label>
						<Col sm="6">
							<Input type="text" id="input-normal" name="input-normal" placeholder="Januari, 2020"  />
						</Col>
					</FormGroup>
					<FormGroup row>
						<Label sm="5" htmlFor="input-normal">Abstract</Label>
						<Col sm="6">
							<Input type="textarea" id="input-normal" name="input-large" placeholder="Ekstraksi topik adalah alat penting untuk membantu mengumpulkan informasi dari sejumlah besar sumber. Makalah ini memperkenalkan pendekatan baru untuk mengekstrak topik dari kumpulan dokumen teks. Untuk mendapatkan topik, langkah preprocessing dilakukan untuk menghapus bagian dokumen yang tidak perlu. Kemudian, istilah frekuensi dokumen invers-frekuensi dibangun berdasarkan bobot dalam dokumen. Setelah itu, transformasi fitur berbasis SVD terlibat dalam membangun fitur yang digunakan untuk pengelompokan. Sebelum proses pengelompokan, algoritma Pilar dijalankan untuk memilih centroid awal untuk pengelompokan K-Means. Akhirnya, bobot istilah dalam cluster dihitung menggunakan term-cluster weight sebagai dasar untuk memilih topik dari cluster. Berdasarkan hasil eksperimental, disimpulkan bahwa kerangka kerja mencapai hasil yang memuaskan dengan mencapai akurasi 100%, 95,1%, 83,7%, dan 68,7% untuk 4 topik yang diperoleh dari kategori Binary2, Multi5, Multi7, dan Multi10 dari 20Newsgroup Himpunan data."  />
						</Col>
					</FormGroup>
          <FormGroup row>
						<Label sm="5" htmlFor="input-normal">File</Label>
						<Col sm="6">
            <Input type="file" name="file" id="exampleFile" />
            <FormText color="muted">
              File harus berformat pdf.
            </FormText>
						</Col>
					</FormGroup>
        <Link to='/profile'>  <Button variant="contained" color="primary">
            Submit Edit
          </Button>
          </Link>
				</Form>
			</CardBody>
		</Card>
		</Col>
		</Row>

	);
}
